"use client";

import { useState, useRef, useCallback } from "react";
import {
  Flame,
  Snowflake,
  Wind,
  MoreVertical,
  Thermometer,
} from "lucide-react";
import type { ClimateProps } from "./widget-types";
import { cn } from "@/lib/utils";
import { useEntityStateStore } from "@/stores/entity-state-store";

const SELECTOR_MIN = 18;
const SELECTOR_STEP = 0.5;
const SWIPE_THRESHOLD_PX = 36;

function formatTemp(value: number | undefined): string {
  if (value == null || Number.isNaN(value)) return "—";
  const rounded = Math.round(value * 2) / 2;
  return rounded % 1 === 0.5 ? `${rounded}°` : `${Math.round(rounded)}°`;
}

const TEMP_MIN = 5;
const TEMP_MAX = 35;

const MODE_CONFIG: { mode: string; label: string; icon: React.ElementType }[] = [
  { mode: "fan_only", label: "Wind", icon: Wind },
  { mode: "cool", label: "Cooling", icon: Snowflake },
  { mode: "heat", label: "Heat", icon: Flame },
];

const STATE_LABELS: Record<string, string> = {
  idle: "Idle",
  heating: "Verwarmen…",
  cooling: "Koelen…",
  off: "Uit",
  heat: "Verwarmen",
  cool: "Koelen",
  auto: "Auto",
  dry: "Droog",
  fan_only: "Ventilator",
  unknown: "—",
};

export function ClimateCard2Widget({
  title = "Air Conditioner",
  entity_id,
  size = "md",
  className,
  onMoreClick,
}: ClimateProps & { className?: string; onMoreClick?: () => void }) {
  const entity = useEntityStateStore((s) => s.getState(entity_id));
  const setStates = useEntityStateStore((s) => s.setStates);
  const [loading, setLoading] = useState(false);

  const currentTemperature = Number(entity?.attributes?.current_temperature) || undefined;
  const targetTemperature = Number(entity?.attributes?.temperature) ?? Number(entity?.attributes?.target_temp_high) ?? undefined;
  const minTemp = Number(entity?.attributes?.min_temp) || TEMP_MIN;
  const maxTemp = Number(entity?.attributes?.max_temp) || TEMP_MAX;
  const hvacModes = (entity?.attributes?.hvac_modes as string[] | undefined) ?? [];
  const state = (entity?.state as string | undefined) ?? "";
  const hvacModeFromAttr = entity?.attributes?.hvac_mode as string | undefined;
  const stateToMode: Record<string, string> = {
    heating: "heat",
    cooling: "cool",
    heat: "heat",
    cool: "cool",
    off: "off",
    auto: "auto",
    idle: "auto",
    dry: "dry",
    fan_only: "fan_only",
  };
  const resolvedMode = hvacModeFromAttr ?? stateToMode[state] ?? state;
  const activeHvacMode =
    resolvedMode && hvacModes.includes(resolvedMode) ? resolvedMode : hvacModes[0] ?? null;
  const isOff = state === "off" || !state;

  const displayTemp = targetTemperature ?? currentTemperature ?? 20;
  const selectorMin = Math.max(SELECTOR_MIN, minTemp);
  const tempOptions = Array.from(
    { length: Math.round((maxTemp - selectorMin) / SELECTOR_STEP) + 1 },
    (_, i) => Math.round((selectorMin + i * SELECTOR_STEP) * 2) / 2
  );

  const swipeStart = useRef<{ x: number; temp: number } | null>(null);
  const didSwipeThisGesture = useRef(false);

  const handleSwipeStart = useCallback(
    (clientX: number) => {
      if (loading || isOff) return;
      swipeStart.current = { x: clientX, temp: displayTemp };
    },
    [loading, isOff, displayTemp]
  );

  const handleSwipeEnd = useCallback(() => {
    swipeStart.current = null;
    if (didSwipeThisGesture.current) {
      setTimeout(() => {
        didSwipeThisGesture.current = false;
      }, 0);
    }
  }, []);

  const setTemperature = useCallback(
    async (temperature: number) => {
      const rounded = Math.round(temperature * 2) / 2;
      const clamped = Math.min(maxTemp, Math.max(selectorMin, rounded));
      setLoading(true);
      try {
        const res = await fetch("/api/ha/call-service", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            entity_id,
            domain: "climate",
            service: "set_temperature",
            service_data: { temperature: clamped },
          }),
        });
        if (res.ok) {
          const data = await fetch("/api/ha/state").then((r) => r.json());
          if (Array.isArray(data)) setStates(data);
        }
      } finally {
        setLoading(false);
      }
    },
    [entity_id, maxTemp, selectorMin, setStates]
  );

  const handleSwipeMoveWithSetTemp = useCallback(
    (clientX: number) => {
      if (!swipeStart.current) return;
      const delta = clientX - swipeStart.current.x;
      if (Math.abs(delta) >= SWIPE_THRESHOLD_PX) {
        didSwipeThisGesture.current = true;
        const steps = Math.floor(delta / SWIPE_THRESHOLD_PX);
        const newTemp = Math.min(
          maxTemp,
          Math.max(selectorMin, Math.round((swipeStart.current.temp + steps * SELECTOR_STEP) * 2) / 2)
        );
        setTemperature(newTemp);
        swipeStart.current = { x: clientX, temp: newTemp };
      }
    },
    [maxTemp, selectorMin, setTemperature]
  );

  async function setHvacMode(hvac_mode: string) {
    setLoading(true);
    try {
      const res = await fetch("/api/ha/call-service", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          entity_id,
          domain: "climate",
          service: "set_hvac_mode",
          service_data: { hvac_mode },
        }),
      });
      if (res.ok) {
        const data = await fetch("/api/ha/state").then((r) => r.json());
        if (Array.isArray(data)) setStates(data);
      }
    } finally {
      setLoading(false);
    }
  }

  async function setPower(on: boolean) {
    setLoading(true);
    try {
      const service = on ? "turn_on" : "turn_off";
      const res = await fetch("/api/ha/call-service", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ entity_id, domain: "climate", service }),
      });
      if (res.ok) {
        const data = await fetch("/api/ha/state").then((r) => r.json());
        if (Array.isArray(data)) setStates(data);
      }
    } finally {
      setLoading(false);
    }
  }

  const availableModes = MODE_CONFIG.filter((c) => hvacModes.includes(c.mode));
  if (availableModes.length === 0 && hvacModes.length > 0) {
    availableModes.push(
      ...hvacModes.slice(0, 3).map((mode) => ({
        mode,
        label: STATE_LABELS[mode] ?? mode,
        icon: Thermometer as React.ElementType,
      }))
    );
  }

  return (
    <div
      className={cn(
        "flex w-full flex-col overflow-hidden rounded-2xl bg-white/15 dark:bg-black/40 text-gray-900 dark:text-white shadow-xl backdrop-blur-2xl border border-white/30 dark:border-white/10",
        size === "sm" && "text-sm",
        size === "md" && "text-base",
        size === "lg" && "text-lg",
        className
      )}
    >
      {/* Header: icon + title | toggle + more */}
      <div className="flex items-center justify-between gap-2 px-4 pt-4 pb-2">
        <div className="flex items-center gap-2 min-w-0">
          <div className="flex h-9 w-9 shrink-0 items-center justify-center rounded-lg bg-white/20 dark:bg-white/10">
            <Thermometer className="h-5 w-5 text-amber-600 dark:text-amber-400" aria-hidden />
          </div>
          <span className="font-medium truncate">{title}</span>
        </div>
        <div className="flex items-center gap-1 shrink-0">
          <button
            type="button"
            onClick={() => setPower(!isOff)}
            disabled={loading}
            role="switch"
            aria-checked={!isOff}
            className={cn(
              "relative inline-flex h-6 w-11 shrink-0 rounded-full border-2 border-transparent transition-colors focus:outline-none focus:ring-2 focus:ring-amber-500 focus:ring-offset-2 focus:ring-offset-transparent disabled:opacity-50",
              !isOff
                ? "bg-amber-500 dark:bg-amber-500"
                : "bg-gray-300 dark:bg-gray-600"
            )}
          >
            <span
              className={cn(
                "pointer-events-none inline-block h-5 w-5 transform rounded-full bg-white shadow ring-0 transition",
                !isOff ? "translate-x-5" : "translate-x-1"
              )}
            />
          </button>
          {onMoreClick && (
            <button
              type="button"
              onClick={onMoreClick}
              className="p-1.5 rounded-lg text-gray-500 hover:bg-white/20 dark:text-gray-400 dark:hover:bg-white/10"
              aria-label="More options"
            >
              <MoreVertical className="h-5 w-5" />
            </button>
          )}
        </div>
      </div>

      {/* Grote temperatuur + status */}
      <div className="px-4 py-2 text-center">
        <p className="text-5xl font-bold tabular-nums text-gray-900 dark:text-white">
          {formatTemp(displayTemp)}
        </p>
        <p className="text-sm text-gray-500 dark:text-gray-400 mt-0.5">
          {isOff ? "Uit" : STATE_LABELS[state] ?? state}
        </p>
      </div>

      {/* Mode-knoppen: Wind, Cooling, Heat */}
      {availableModes.length > 0 && (
        <div className="px-4 py-2">
          <div
            className="flex rounded-xl bg-gray-200/50 dark:bg-white/10 p-1"
            role="tablist"
          >
            {availableModes.map(({ mode, label, icon: Icon }) => (
              <button
                key={mode}
                type="button"
                role="tab"
                aria-selected={activeHvacMode === mode}
                onClick={() => setHvacMode(mode)}
                disabled={loading || isOff}
                className={cn(
                  "flex-1 flex items-center justify-center gap-1.5 rounded-lg py-2.5 text-sm font-medium transition-colors disabled:opacity-50",
                  activeHvacMode === mode
                    ? "bg-amber-400 dark:bg-amber-500 text-gray-900 shadow-sm"
                    : "text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white"
                )}
              >
                <Icon className="h-4 w-4" aria-hidden />
                {label}
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Temperatuur-schuif (vanaf 18, tap of swipe) */}
      <div className="px-4 pb-4 pt-2">
        <div
          className="flex items-center gap-1 overflow-x-auto overflow-y-hidden pb-1 touch-none select-none min-w-0 scrollbar-hide"
          onTouchStart={(e) => handleSwipeStart(e.touches[0].clientX)}
          onTouchMove={(e) => handleSwipeMoveWithSetTemp(e.touches[0].clientX)}
          onTouchEnd={handleSwipeEnd}
          onTouchCancel={handleSwipeEnd}
          onMouseDown={(e) => {
            e.preventDefault();
            handleSwipeStart(e.clientX);
          }}
          onMouseMove={(e) => {
            if (swipeStart.current) handleSwipeMoveWithSetTemp(e.clientX);
          }}
          onMouseUp={handleSwipeEnd}
          onMouseLeave={handleSwipeEnd}
        >
          {tempOptions.map((t) => (
            <button
              key={t}
              type="button"
              onClick={(e) => {
                if (didSwipeThisGesture.current) {
                  e.preventDefault();
                  e.stopPropagation();
                  return;
                }
                setTemperature(t);
              }}
              disabled={loading || isOff}
              className={cn(
                "flex h-9 w-9 shrink-0 items-center justify-center rounded-full text-sm font-medium transition-colors disabled:opacity-50",
                Math.round(displayTemp * 2) / 2 === t
                  ? "bg-amber-400 dark:bg-amber-500 text-gray-900 ring-2 ring-amber-600/50 dark:ring-amber-400/50"
                  : "text-gray-600 dark:text-gray-400 hover:bg-white/20 dark:hover:bg-white/10"
              )}
            >
              {t}
            </button>
          ))}
        </div>
        <p className="text-xs text-gray-500 dark:text-gray-400 mt-1.5 text-center">
          Tik op een getal of veeg om temperatuur aan te passen
        </p>
      </div>
    </div>
  );
}
