"use client";

import { useState, useEffect } from "react";
import {
  Sparkles,
  ChevronDown,
  ChevronUp,
  Bot,
  Home,
  CircleDot,
  Activity,
  Zap,
  Box,
  Car,
  Building2,
  Sofa,
  Lamp,
  TreePine,
  Shirt,
  UtensilsCrossed,
  BedDouble,
  Bath,
  DoorOpen,
  Circle,
  Star,
} from "lucide-react";
import type { VacuumCardProps } from "./widget-types";
import { cn } from "@/lib/utils";
import { useEntityStateStore } from "@/stores/entity-state-store";

const VACUUM_ICONS: Record<string, React.ComponentType<{ className?: string; "aria-hidden"?: boolean }>> = {
  Activity,
  Bath,
  BedDouble,
  Bot,
  Box,
  Building2,
  Car,
  Circle,
  CircleDot,
  DoorOpen,
  Home,
  Lamp,
  Shirt,
  Sofa,
  Sparkles,
  Star,
  TreePine,
  UtensilsCrossed,
  Zap,
};

export const VACUUM_ICON_OPTIONS = Object.keys(VACUUM_ICONS).sort();

const STATE_LABELS: Record<string, string> = {
  idle: "Inactief",
  cleaning: "Bezig met stofzuigen",
  docked: "In basis",
  returning: "Keert terug",
  paused: "Gepauzeerd",
  error: "Fout",
  unknown: "—",
};

export function VacuumCardWidget({
  title = "Stofzuiger",
  entity_id,
  script_ids = [],
  script_names = {},
  cleaned_area_entity_id,
  icon: iconName,
  size = "md",
  className,
}: VacuumCardProps & { className?: string }) {
  const entity = useEntityStateStore((s) => s.getState(entity_id));
  const cleanedAreaEntity = useEntityStateStore((s) =>
    cleaned_area_entity_id ? s.getState(cleaned_area_entity_id) : null
  );
  const setStates = useEntityStateStore((s) => s.setStates);
  const [loadingScript, setLoadingScript] = useState<string | null>(null);
  /** Script die gestart is; blijft actief tot vacuum weer "docked" is. */
  const [activeScriptId, setActiveScriptId] = useState<string | null>(null);
  const [roomsExpanded, setRoomsExpanded] = useState(false);

  const state = (entity?.state as string) ?? "unknown";
  const friendlyName = (entity?.attributes?.friendly_name as string) ?? entity_id;
  const statusLabel = STATE_LABELS[state] ?? state;

  const cleanedAreaValue = cleanedAreaEntity?.state;
  const cleanedAreaUnit = (cleanedAreaEntity?.attributes as { unit_of_measurement?: string })?.unit_of_measurement ?? "";
  const showCleanedArea = cleaned_area_entity_id && cleanedAreaValue != null && cleanedAreaValue !== "";

  const IconComponent = (iconName && VACUUM_ICONS[iconName]) ? VACUUM_ICONS[iconName] : Sparkles;
  const isActive = state === "cleaning" || activeScriptId != null;

  useEffect(() => {
    if (state === "docked" && activeScriptId) {
      setActiveScriptId(null);
    }
  }, [state, activeScriptId]);

  async function runScript(scriptEntityId: string) {
    setLoadingScript(scriptEntityId);
    try {
      const res = await fetch("/api/ha/call-service", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          entity_id: scriptEntityId,
          domain: "script",
          service: "turn_on",
        }),
      });
      if (res.ok) {
        const data = await fetch("/api/ha/state").then((r) => r.json());
        if (Array.isArray(data)) setStates(data);
        setActiveScriptId(scriptEntityId);
      }
    } finally {
      setLoadingScript(null);
    }
  }

  const isAnyScriptActive = activeScriptId != null;

  return (
    <div
      className={cn(
        "flex w-full flex-col overflow-hidden rounded-2xl bg-white/10 dark:bg-black/50 text-white shadow-xl backdrop-blur-2xl border border-white/20 dark:border-white/10",
        size === "sm" && "text-sm",
        size === "md" && "text-base",
        size === "lg" && "text-lg",
        className
      )}
    >
      <div className="flex items-center justify-between gap-3 px-4 pt-4">
        <div className="flex items-center gap-3 min-w-0">
          <div
            className={cn(
              "flex h-10 w-10 shrink-0 items-center justify-center rounded-full bg-white/15 text-white",
              isActive && "animate-vacuum"
            )}
          >
            <IconComponent className="h-5 w-5" aria-hidden />
          </div>
          <div className="min-w-0">
            <p className="font-medium truncate text-white/90">{title}</p>
            <p className="text-sm text-white/70 truncate">{friendlyName}</p>
          </div>
        </div>
        <div className="flex flex-col items-end shrink-0 min-w-0 max-w-[45%]">
          <p
            className={cn(
              "text-sm font-medium truncate w-full text-right",
              state === "cleaning" && "text-amber-400",
              state === "docked" && "text-green-400",
              state === "error" && "text-red-400",
              !["cleaning", "docked", "error"].includes(state) && "text-white/80"
            )}
            title={statusLabel}
          >
            {statusLabel}
          </p>
          {showCleanedArea && (
            <p className="text-xs text-white/60 truncate w-full text-right">
              {cleanedAreaValue}{cleanedAreaUnit ? ` ${cleanedAreaUnit}` : ""}
            </p>
          )}
        </div>
      </div>

      {script_ids.length > 0 && (
        <div className="px-4 pb-3 pt-4 mt-4 border-t border-white/10">
          <button
            type="button"
            onClick={() => setRoomsExpanded((v) => !v)}
            className="flex items-center gap-1.5 w-full text-left text-[11px] text-white/60 hover:text-white/80 transition-colors"
            aria-expanded={roomsExpanded}
          >
            <span>Rooms</span>
            {roomsExpanded ? (
              <ChevronUp className="h-3.5 w-3.5 shrink-0" aria-hidden />
            ) : (
              <ChevronDown className="h-3.5 w-3.5 shrink-0" aria-hidden />
            )}
          </button>
          {roomsExpanded && (
            <div className="flex flex-wrap gap-1 mt-1.5">
              {script_ids.map((scriptId) => (
                <ScriptTag
                  key={scriptId}
                  entityId={scriptId}
                  displayName={script_names?.[scriptId]}
                  onRun={() => runScript(scriptId)}
                  loading={loadingScript === scriptId}
                  active={activeScriptId === scriptId}
                  disabled={isAnyScriptActive && activeScriptId !== scriptId}
                />
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
}

function ScriptTag({
  entityId,
  displayName,
  onRun,
  loading,
  active,
  disabled,
}: {
  entityId: string;
  displayName?: string;
  onRun: () => void;
  loading: boolean;
  active?: boolean;
  disabled?: boolean;
}) {
  const entity = useEntityStateStore((s) => s.getState(entityId));
  const label =
    displayName ?? entity?.attributes?.friendly_name ?? entityId.replace(/^script\./, "");

  return (
    <button
      type="button"
      onClick={onRun}
      disabled={loading || disabled}
      className={cn(
        "inline-flex items-center rounded-md border px-2 py-0.5 text-[11px] font-medium transition-colors",
        active
          ? "border-amber-400/60 bg-amber-500/25 text-amber-200"
          : "border-white/25 bg-white/10 text-white/95 hover:bg-white/20 hover:border-white/40",
        (loading || disabled) && !active && "opacity-50 cursor-not-allowed"
      )}
    >
      <span className="truncate max-w-[7rem]">
        {loading ? "…" : active ? `${label} (bezig)` : label}
      </span>
    </button>
  );
}
